(function() {
  'use strict';

  angular
    .module('memorialacademico')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('memorialacademico')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('memorialacademico')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function () {
  'use strict';

  angular
    .module('memorialacademico')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController(toastr, $state, $stateParams, $http, $window, $log) {
    var vm = this;
    vm.showToastr = showToastr;
    $http.get("http://memorialacademico.com.br/estatistica").then(function (response) {
      vm.visitantes = response.data.accessTotalNum;
    });
    $http.get("http://memorialacademico.com.br/admin/mensagem").then(function (response) {
      vm.messages = response.data;
    });

    if (!$window.sessionStorage.getItem("access")) {
      $window.sessionStorage.setItem("access", "on");
      $http.get("http://memorialacademico.com.br/listaIndicados").then(function (response) {
        vm.listaNomesRemote = response.data;
      });
    } else {
      $http.get("http://memorialacademico.com.br/listaIndicados/same").then(function (response) {
        vm.listaNomesRemote = response.data;
      });
    }

    vm.formName='';
    vm.formEmail='';
    vm.formMessage='';
    vm.formJournal='';

    vm.formSendDetails = function(name, email, message, journal) {

      var formData = {
        'name': name,
        'email': email,
        'message': message,
        'journal': journal
      };

      $http({
        method: 'POST',
        url: 'http://memorialacademico.com.br/mensagem',
        data: formData,
        headers: {'Content-Type' : 'application/json'}
      }).success(function(){
        showToastr("Mensagem enviada com sucesso!");
      }).error(function(e) {
        console.log(e);
      });


    };

    vm.orderItem = angular.identity;

    function showToastr(text) {
      toastr.info(text);
      vm.classAnimation = 'fade';
    }

  }
})();

